$(document).ready(function(){
  $('#menu-icon').on('click', function(){
    $('.navbar').toggleClass('expand');
    return false;
  });

  $('img.phone-img').on('click', function() {
    $(this).toggleClass('active-img');
  });
  $('img.phone-img').on('click', function() {
    $('.price-find-wrap').toggleClass('price-active');
  });
});

// Services
$('#phone-type').change(function() {
  var element = $(this),
      elementValue = element.val();

  switch(elementValue ){
    case 'iphone':
      $('#default-phone-model').removeClass('show-select');
      $('#iphone-model').addClass('show-select');
      $('#samsung-model').removeClass('show-select');
      $('#hauwei-model').removeClass('show-select');
      $('#nokia-model').removeClass('show-select');

      $('#default-phone-model').addClass('hide-select');
      $('#iphone-model').removeClass('hide-select');
      $('#samsung-model').addClass('hide-select');
      $('#hauwei-model').addClass('hide-select');
      $('#nokia-model').addClass('hide-select');
      break;
    case 'samsung':
      $('#default-phone-model').removeClass('show-select');
      $('#iphone-model').removeClass('show-select');
      $('#samsung-model').addClass('show-select');
      $('#hauwei-model').removeClass('show-select');
      $('#nokia-model').removeClass('show-select');

      $('#default-phone-model').addClass('hide-select');
      $('#iphone-model').addClass('hide-select');
      $('#samsung-model').removeClass('hide-select');
      $('#hauwei-model').addClass('hide-select');
      $('#nokia-model').addClass('hide-select');
      break;
    case 'huawei':
      $('#default-phone-model').removeClass('show-select');
      $('#iphone-model').removeClass('show-select');
      $('#samsung-model').removeClass('show-select');
      $('#hauwei-model').addClass('show-select');
      $('#nokia-model').removeClass('show-select');

      $('#default-phone-model').addClass('hide-select');
      $('#iphone-model').addClass('hide-select');
      $('#samsung-model').addClass('hide-select');
      $('#hauwei-model').removeClass('hide-select');
      $('#nokia-model').addClass('hide-select');
      break;
    case 'nokia':
          $('#default-phone-model').removeClass('show-select');
          $('#iphone-model').removeClass('show-select');
          $('#samsung-model').removeClass('show-select');
          $('#hauwei-model').removeClass('show-select');
          $('#nokia-model').addClass('show-select');

          $('#default-phone-model').addClass('hide-select');
          $('#iphone-model').addClass('hide-select');
          $('#samsung-model').addClass('hide-select');
          $('#hauwei-model').addClass('hide-select');
          $('#nokia-model').removeClass('hide-select');
          break;
        default:
          $('#default-phone-model').addClass('show-select');
          $('#iphone-model').removeClass('show-select');
          $('#samsung-model').removeClass('show-select');
          $('#hauwei-model').removeClass('show-select');
          $('#nokia-model').removeClass('show-select');

          $('#default-phone-model').removeClass('hide-select');
          $('#iphone-model').addClass('hide-select');
          $('#samsung-model').addClass('hide-select');
          $('#hauwei-model').addClass('hide-select');
          $('#nokia-model').addClass('hide-select');
          break;
      }
    });
